import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Typography,
  Chip,
  Box,
  Tabs,
  Tab,
  Pagination,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  List,
  ListItem,
  ListItemText,
  Divider,
  Alert,
  CircularProgress,
  IconButton,
  Tooltip,
} from '@mui/material';
import { Refresh as RefreshIcon } from '@mui/icons-material';
import axios from 'axios';

const OrderList = () => {
  const navigate = useNavigate();
  const [orders, setOrders] = useState([]);
  const [selectedStatus, setSelectedStatus] = useState('ALL');
  const [page, setPage] = useState(0);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchOrders = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await axios.get('/api/orders');
      setOrders(response.data);
    } catch (err) {
      setError('     .');
      console.error('Error fetching orders:', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchOrders();
  }, []);

  const handleStatusChange = async (orderId, newStatus) => {
    try {
      await axios.put(`/api/orders/${orderId}/status`, { status: newStatus });
      fetchOrders();
    } catch (err) {
      console.error('Error updating order status:', err);
    }
  };

  const handleViewDetails = (order) => {
    setSelectedOrder(order);
    setOpenDialog(true);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'PENDING': return 'warning';
      case 'CONFIRMED': return 'info';
      case 'PREPARING': return 'primary';
      case 'READY': return 'success';
      case 'DELIVERING': return 'secondary';
      case 'COMPLETED': return 'success';
      case 'CANCELLED': return 'error';
      default: return 'default';
    }
  };

  const getStatusLabel = (status) => {
    switch (status) {
      case 'PENDING': return ' ';
      case 'CONFIRMED': return ' ';
      case 'PREPARING': return ' ';
      case 'READY': return ' ';
      case 'DELIVERING': return ' ';
      case 'COMPLETED': return '';
      case 'CANCELLED': return '';
      default: return status;
    }
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Alert severity="error" sx={{ mt: 2 }}>
        {error}
      </Alert>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Box sx={{ mb: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h4"> </Typography>
        <Button
          variant="contained"
          color="primary"
          onClick={() => navigate('/orders/new')}
        >
           
        </Button>
      </Box>

      <Paper sx={{ mb: 3 }}>
        <Tabs
          value={selectedStatus}
          onChange={(e, newValue) => {
            setSelectedStatus(newValue);
            setPage(0);
          }}
          variant="scrollable"
          scrollButtons="auto"
        >
          <Tab label="" value="ALL" />
          <Tab label=" " value="PENDING" />
          <Tab label=" " value="CONFIRMED" />
          <Tab label=" " value="PREPARING" />
          <Tab label=" " value="READY" />
          <Tab label=" " value="DELIVERING" />
          <Tab label="" value="COMPLETED" />
          <Tab label="" value="CANCELLED" />
        </Tabs>
      </Paper>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell> </TableCell>
              <TableCell> </TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {orders
              .filter(order => selectedStatus === 'ALL' || order.status === selectedStatus)
              .slice(page * 10, (page + 1) * 10)
              .map((order) => (
                <TableRow key={order.id}>
                  <TableCell>{order.id}</TableCell>
                  <TableCell>{new Date(order.orderDate).toLocaleString()}</TableCell>
                  <TableCell>{order.customerName}</TableCell>
                  <TableCell>{order.customerPhone}</TableCell>
                  <TableCell>{order.totalAmount.toLocaleString()}</TableCell>
                  <TableCell>
                    <Chip
                      label={getStatusLabel(order.status)}
                      color={getStatusColor(order.status)}
                    />
                  </TableCell>
                  <TableCell>
                    <Box sx={{ display: 'flex', gap: 1 }}>
                      <Button
                        variant="outlined"
                        size="small"
                        onClick={() => handleViewDetails(order)}
                      >
                        
                      </Button>
                      {order.status === 'PENDING' && (
                        <Button
                          variant="contained"
                          color="primary"
                          size="small"
                          onClick={() => handleStatusChange(order.id, 'CONFIRMED')}
                        >
                          
                        </Button>
                      )}
                      {order.status === 'CONFIRMED' && (
                        <Button
                          variant="contained"
                          color="primary"
                          size="small"
                          onClick={() => handleStatusChange(order.id, 'PREPARING')}
                        >
                           
                        </Button>
                      )}
                      {order.status === 'PREPARING' && (
                        <Button
                          variant="contained"
                          color="primary"
                          size="small"
                          onClick={() => handleStatusChange(order.id, 'READY')}
                        >
                           
                        </Button>
                      )}
                      {order.status === 'READY' && (
                        <Button
                          variant="contained"
                          color="primary"
                          size="small"
                          onClick={() => handleStatusChange(order.id, 'DELIVERING')}
                        >
                           
                        </Button>
                      )}
                      {order.status === 'DELIVERING' && (
                        <Button
                          variant="contained"
                          color="primary"
                          size="small"
                          onClick={() => handleStatusChange(order.id, 'COMPLETED')}
                        >
                           
                        </Button>
                      )}
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
        <Pagination
          count={Math.ceil(orders.length / 10)}
          page={page + 1}
          onChange={(e, value) => setPage(value - 1)}
          color="primary"
        />
      </Box>

      <Dialog
        open={openDialog}
        onClose={() => setOpenDialog(false)}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>  </DialogTitle>
        <DialogContent>
          {selectedOrder && (
            <List>
              <ListItem>
                <ListItemText
                  primary=" "
                  secondary={selectedOrder.id}
                />
              </ListItem>
              <ListItem>
                <ListItemText
                  primary=" "
                  secondary={new Date(selectedOrder.orderDate).toLocaleString()}
                />
              </ListItem>
              <ListItem>
                <ListItemText
                  primary=" "
                  secondary={`${selectedOrder.customerName} (${selectedOrder.customerPhone})`}
                />
              </ListItem>
              <ListItem>
                <ListItemText
                  primary=""
                  secondary={selectedOrder.deliveryAddress}
                />
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemText
                  primary=" "
                  secondary={
                    <List>
                      {selectedOrder.orderItems.map((item) => (
                        <ListItem key={item.id}>
                          <ListItemText
                            primary={`${item.pizza.name} (${item.size}) x ${item.quantity}`}
                            secondary={`${item.price.toLocaleString()}`}
                          />
                        </ListItem>
                      ))}
                    </List>
                  }
                />
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemText
                  primary="  "
                  secondary={`${selectedOrder.totalAmount.toLocaleString()}`}
                />
              </ListItem>
            </List>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}></Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default OrderList; 